// CertDetail Componet

import React from 'react'
import { useHistory } from "react-router-dom";

import './cert-detail.scss'
import {getCert} from '../../../services/certs'
import TechBubble from '../../../components/TechBubble'
import CertBubble from '../../../components/CertBubble'
import getTech from '../../../services/techs'

const CertDetail = ({ match }) => {
  let history = useHistory();
  let uri = match.params.cert
  const cert = getCert('uri', uri)
  let certImg = ''

  if(cert){
    certImg = require(`../../../assets/images/techs/${cert.img || 'school.png'}`)
  } else {
    history.push('/training')
  }

  return certImg === "" ? null : (
    <div className="certDetail">
      <figure className="certDetail-img">
        <img src={certImg.default} alt={cert.name} loading="lazy" />
      </figure>
      <h2>{cert.name}</h2>
      <section className="certDetail-info">
        <div className="certDetail-infoItem">
          <h4>Fecha</h4>
          <p>{cert.dates}</p>
        </div>
        <div className="certDetail-infoItem">
          <h4>Centro</h4>
          <p>{cert.school}</p>
        </div>
        <div className="certDetail-infoItem">
          <h4>Modalidad</h4>
          <p>{cert.mode}</p>
        </div>
        {
          cert.hours &&
          <div className="certDetail-infoItem">
            <h4>Horas</h4>
            <p>{cert.hours}</p>
          </div>
        }
        {
          cert.credtId &&
          <div className="certDetail-infoItem">
            <h4>Código</h4>
            <p>{cert.credtId}</p>
          </div>
        }
      </section>
      {
        cert.type === 'career' && cert.courses.length &&
        <section className="certDetail-courses">
          <h3>Cursos de la carrera</h3>
          <div className="certDetail-coursesList">
          {
            cert.courses.map(id => {
              const course = getCert('id', id)
              return <CertBubble {...course} key={id} />
            })
          }
          </div>
        </section>
      }

      {
        !cert.techs.length ? '' :
        <section className="certDetail-techs">
          <h3>Tecnologías Impartidas</h3>
          <div className="certDetail-techList">
            {
              cert.techs.map((item) => {
                const tech = getTech(item);
                return <TechBubble
                          key={tech.id}
                          img={tech.img}
                          name={tech.name}
                          url={tech.url}
                        />
              })
            }
          </div>
        </section>
      }
    </div>
  )
}

export default CertDetail
